<template>
  <div class="box">
    <div class="banner">
      <carousel></carousel>
      <div class="search">
        <p>Welcome to Help Center</p>
        <a-input-search v-model="text" placeholder="Search for support" @search="onSearch" @change="onChange" :loading='searchLoading' />
      </div>
    </div>
    <div class="body">
      <div class="left" v-show="!searchviewShow">
        <collapseLeft></collapseLeft>
      </div>
      <div class="right" v-show="!searchviewShow">
        <collapseRight></collapseRight>
      </div>
      <div class="searchView" v-show="searchviewShow">
        <searchview ref="searchViewList" :List='List' :SearchText='text' :activeKey='searchActiveKey'></searchview>
      </div>
    </div>
  </div>
</template>

<script>
  import faq from '@/api/faq';
  import carousel from './header/Carousel.vue';
  import collapseLeft from './header/Collapse_left.vue';
  import collapseRight from './header/Collapse_right.vue';
  import searchview from './header/SearchView.vue';
  export default {
    props: {},
    components: {
      carousel,
      collapseLeft,
      collapseRight,
      searchview
    },
    data() {
      return {
        text: '',
        customStyle: 'border: none;overflow: hidden',
        searchLoading: false,
        searchviewShow: false,
        List: {},
        searchActiveKey: []
      };
    },
    methods: {
      onChange(e) {
        this.text = e.target.value
      },
      onSearch() {
        this.searchLoading = true
        if (this.text.trim().length > 0) {
          this.SearchView(this.text.trim())
          this.searchLoading = false
        } else {
          console.log('搜索框为空');
          this.searchLoading = false
        }
      },
      SearchView(val) {
        faq.getSearchFaq({ Search: val }).then(res => {
          if (res.data.Code === 200) {
            if (res.data.ArticleList.length > 0) {
              this.List = res.data
              this.searchviewShow = true
              this.searchActiveKey = []
              this.searchActiveKey.push(this.List.ArticleList[0].ArticleId)
            } else {
              console.log('未查询到数据');
              this.List = res.data
              this.searchviewShow = true
              this.searchActiveKey = []
            }
          } else {
            console.log('查询失败');
          }
        })
      }
    },
    watch: {
      text() {
        this.searchviewShow = false
      }
    }
  };
</script>

<style scoped lang='less'>
  .box {
    width: 100%;
  }

  .banner {
    width: 100%;
    margin: 0 auto;
  }

  .search {
    position: absolute;
    top: 80px;
    left: 50%;
    transform: translate(-50%, -0%);
    width: 588px;
    height: 140px;
    text-align: center;

    p {
      font-size: 32px;
      font-family: Inter-Regular_Bold, Inter;
      font-weight: 500;
      color: #FFFFFF;
      line-height: 38px;
      text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
      margin-bottom: 66px;
    }
  }

  .body {
    width: 63.3%;
    margin: 26px auto 46px auto;
    display: flex;
    justify-content: space-between;

    .left {
      width: 298px;
    }

    .right {
      width: 73.6%;
    }

    .searchView {
      width: 100%;
    }
  }

  ::v-deep .ant-input {
    height: 36px;
    padding: 11px 16px;
    font-size: 14px;
    font-family: Inter-Regular, Inter;
    font-weight: 400;
    color: #292929;
  }

  ::v-deep .ant-input::placeholder {
    color: #B4B4B4;
  }

  ::v-deep .anticon svg {
    width: 16px;
    height: 16px;
  }

  ::v-deep .ant-collapse {
    background: #fff;
  }

  ::v-deep .blockquotebox {
    display: block;
    border-left: 8px solid #d0e5f2;
    padding: 5px 10px;
    margin: 10px 0;
    line-height: 1.4;
    font-size: 100%;
    background-color: #f1f1f1;
  }

  @media screen and (max-width: 1500px) {
    .body .right {
      width: 100%;
    }
  }

  @media screen and (max-width: 1200px) {
    .left {
      display: none;
    }

    .body {
      width: 70%;
    }
  }

  @media screen and (max-width: 1000px) {
    .body {
      width: 80%;
    }
  }

  @media screen and (max-width: 800px) {
    .body {
      width: 100%;
    }
  }


  @media screen and (max-width: 588px) {
    .search {
      width: 100%;
    }
  }
</style>